import {useState, Fragment } from "react";
import { createPortal } from "react-dom";
import ErrorModal from "../Modal/ErrorModal";
import styles from "./UpdateCustomer.module.css";
import Button from "../UI/Button";
function UpdateCustomer(props) {
  const [enteredFirstName, setFirstName] = useState(props.customer.firstName);
  const [enteredLastName, setLastName] = useState(props.customer.lastName);
  const [enteredPhone, setPhone] = useState(props.customer.phone);
  const [enteredEmail, setEmail] = useState(props.customer.email);
  const [updateErrorMessage,setUpdateErrorMessage]=useState(null);

  function updateCustomerHandler(event){
    event.preventDefault();
    if (enteredEmail.trim() === "") {
      setUpdateErrorMessage('Email is required');
    } else {
      setUpdateErrorMessage(null);
      updateCustomer();
    }
  }

  async function updateCustomer() {
    setUpdateErrorMessage(null);
    const customer = {
      id: props.customer.id,
      firstName: enteredFirstName,
      lastName: enteredLastName,
      email: enteredEmail.trim(),
      phone: enteredPhone,
    };
   
    try {
      const response = await fetch("/customers", {
        method: "POST",
        body: JSON.stringify(customer),
        headers: {
          "Content-Type": "application/json",
        },
      });
      if (!response.ok) {
        let errorMessage = "Something went wrong";
        if (response.status !== 500) {
          const data = await response.json();
          if (data.message.includes("unique_email")) {
            errorMessage = "Email already exists";
          }
        }
        throw new Error(errorMessage);
      }
      props.onUpdateCustomer(customer);
      props.closeUpdateMenu();
    } catch (error) {
      setUpdateErrorMessage(error.message);
     
    }
  }

  
  function onChangeFirstNameHandler(event) {
    setFirstName(event.target.value);
  }
  function onChangeLastNameHandler(event) {
    setLastName(event.target.value);
  }
  function onChangePhoneHandler(event) {
    setPhone(event.target.value);
  }
  function onChangeEmailHandler(event) {
    setEmail(event.target.value);
  }

  function updateModalHandler() {
    setUpdateErrorMessage(null);
  }
  const destination = document.getElementById("modal-destination");
  return (
    <Fragment>
      {updateErrorMessage &&
        createPortal(
          <ErrorModal onClose={updateModalHandler} errorMessage={updateErrorMessage}></ErrorModal>,
          destination
        )}
      <td className={styles["table-cell"]}>
        <input
          className={styles["update-input"]}
          name="firstName"
          type="text"
          placeholder="First name"
          value={enteredFirstName}
          onChange={onChangeFirstNameHandler}
        ></input>
      </td>
      <td className={styles["table-cell"]}>
        <input
          className={styles["update-input"]}
          name="lastName"
          type="text"
          placeholder="Last name"
          value={enteredLastName}
          onChange={onChangeLastNameHandler}
        ></input>
      </td>
      <td className={styles["table-cell"]}>
        <input
          className={styles["update-input"]}
          name="email"
          type="text"
          placeholder="Email adress"
          value={enteredEmail}
          onChange={onChangeEmailHandler}
        ></input>
      </td>
      <td className={styles["table-cell"]}>
        <input
          className={styles["update-input"]}
          name="phone"
          type="text"
          placeholder="Phone number"
          value={enteredPhone}
          onChange={onChangePhoneHandler}
        ></input>
      </td>

      <td className={styles["table-cell-buttons"]}>
        <Button
          buttonText="Save"
          additionalClass="save-update-button"
          clickHandler={updateCustomerHandler}
        ></Button>
        <Button
          buttonText="Cancel"
          additionalClass="cancel-update-button"
          clickHandler={props.closeUpdateMenu}
        ></Button>
      </td>
    </Fragment>
  );
}

export default UpdateCustomer;
