import { Fragment, useState } from "react";
import { createPortal } from "react-dom";
import styles from "./AddCustomer.module.css";
import { Link } from "react-router-dom";
import Button from "../UI/Button";
import ErrorModal from "../Modal/ErrorModal";
function AddCustomer(props) {
  const [enteredFirstName, setFirstName] = useState("");
  const [enteredLastName, setLastName] = useState("");
  const [enteredPhone, setPhone] = useState("");
  const [enteredEmail, setEmail] = useState("");
  const [addCustomerSuccess, setCustomerSuccess] = useState(false);
  const [isRequired, setIsRequired] = useState(false);
  const [error, setError] = useState(null);

  function addCustomerHandler(event) {
    event.preventDefault();
    if (enteredEmail.trim() === "") {
      setIsRequired(true);
    } else {
      setIsRequired(false);
      addCustomer();
    }
  }

  async function addCustomer() {
    setError(null);
    const customer = {
      id: 0,
      firstName: enteredFirstName,
      lastName: enteredLastName,
      email: enteredEmail.trim(),
      phone: enteredPhone,
    };
    try {
      const response = await fetch("/customers", {
        method: "POST",
        body: JSON.stringify(customer),
        headers: {
          "Content-Type": "application/json",
        },
      });

      if (!response.ok) {
        let errorMessage = "Something went wrong";
        if (response.status !== 500) {
          const data = await response.json();
          if (data.message.includes("unique_email")) {
            errorMessage = "Email already exists";
          }
        }
        throw new Error(errorMessage);
      }
      const data = await response.json();
      props.onAddCustomer(data);
      setCustomerSuccess(true);
      removeSuccessMessage();
      setEmail("");
      setFirstName("");
      setLastName("");
      setPhone("");
    } catch (error) {
      setError(error.message);
    }
  }
  function removeSuccessMessage() {
    setTimeout(() => {
      setCustomerSuccess(false);
    }, 1500);
  }
  function onChangeFirstNameHandler(event) {
    setFirstName(event.target.value);
  }
  function onChangeLastNameHandler(event) {
    setLastName(event.target.value);
  }
  function onChangePhoneHandler(event) {
    setPhone(event.target.value);
  }
  function onChangeEmailHandler(event) {
    setEmail(event.target.value);
  }

  function closeErrorModal() {
    setError(null);
  }

  const message= (addCustomerSuccess&& "Customer added successfully!")||(isRequired&&'Email is required.');
  const destination = document.getElementById("modal-destination");
  return (
    <Fragment>
      {error &&
        createPortal(
          <ErrorModal
            onClose={closeErrorModal}
            errorMessage={error}
          ></ErrorModal>,
          destination
        )}
      {/* {isRequired && <h3 className={styles.warning}>Email is required.</h3>} */}
      <span className={styles["success-message"]}>
        {message}
      </span>
      <form className={styles["add-form"]} onSubmit={addCustomerHandler}>
        <div className={styles["form-group"]}>
          <h3 className={styles.text}>First Name:</h3>

          <input
            className={styles["add-input"]}
            name="firstName"
            type="text"
            value={enteredFirstName}
            onChange={onChangeFirstNameHandler}
          ></input>
        </div>
        <div className={styles["form-group"]}>
          <h3 className={styles.text}>Last Name:</h3>
          <input
            className={styles["add-input"]}
            name="lastName"
            type="text"
            value={enteredLastName}
            onChange={onChangeLastNameHandler}
          ></input>
        </div>
        <div className={styles["form-group"]}>
          <h3 className={styles.text}>Phone:</h3>
          <input
            className={styles["add-input"]}
            name="phone"
            type="text"
            value={enteredPhone}
            onChange={onChangePhoneHandler}
          ></input>
        </div>
        <div className={styles["form-group"]}>
          <h3 className={styles.text}>Email:</h3>
          <input
            className={`${styles["add-input"]} ${
              isRequired && styles["input-required"]
            }`}
            name="email"
            type="text"
            value={enteredEmail}
            onChange={onChangeEmailHandler}
          ></input>
        </div>
        <div className={styles["button-container"]}>
          <Button
            buttonText="Save"
            additionalClass="save-customer-button"
          ></Button>
          <Link to="/" className={styles["back-link"]}>
            Back
          </Link>
        </div>
      </form>
    </Fragment>
  );
}

export default AddCustomer;
