import { Fragment } from "react";
import styles from "./CustomerDisplay.module.css";

function CustomerDisplay(props) {
  return (
    <Fragment>
      <td className={styles["table-cell"]}>
        {props.customer.firstName + "  "}
      </td>
      <td className={styles["table-cell"]}>{props.customer.lastName + "  "}</td>
      <td className={styles["table-cell"]}>{props.customer.email + "  "}</td>
      <td className={styles["table-cell"]}>{props.customer.phone + "  "}</td>
    </Fragment>
  );
}

export default CustomerDisplay;
