import { Fragment, useState } from "react";
import CustomerDisplay from "./CustomerDisplay";
import UpdateCustomer from "./UpdateCustomer";
import DropdownMenu from "../UI/DropdownMenu";
import DeleteUserModal from "../Modal/DeleteUserModal";
import { createPortal } from "react-dom";
import {enableBodyScroll, disableBodyScroll} from 'body-scroll-lock';

function CustomerRow(props) {
  const [customer, setCustomer] = useState(props.customer);
  const [showMenu, setShowMenu] = useState(false);
  const [updateCustomer, setUpdateCustomer] = useState(false);
  const [showDeleteCustomerModal, setDeleteCustomerModal] = useState(false);

  function dropdownMenuHandler() {
    setShowMenu(showMenu ? false : true);
  }
 
  function openUpdateMenuHandler(event) {
    event.preventDefault();
    setUpdateCustomer(true);
  }

  
  function closeUpdateMenuHandler() {
    setUpdateCustomer(false);
  }
  
  function updateCustomerHandler(customer) {
    setCustomer(customer);
    props.onUpdate(customer);
  }

  function openDeleteCustomerModal(event) {
    event.preventDefault();
    setDeleteCustomerModal(true);
    disableBodyScroll(document);
  }

  function deleteCustomerHandler() {
    setDeleteCustomerModal(false);
    props.onDelete(customer);
    enableBodyScroll(document);
  }

  function cancelDeleteCustomer() {
    setDeleteCustomerModal(false);
    enableBodyScroll(document);
  }

  const destination = document.getElementById("modal-destination");

  return (
    <Fragment>
     
      {updateCustomer ? (
        <UpdateCustomer
          onUpdateCustomer={updateCustomerHandler}
          closeUpdateMenu={closeUpdateMenuHandler}
          customer={customer}
        ></UpdateCustomer>
      ) : (
        <Fragment>
          <CustomerDisplay customer={customer}></CustomerDisplay>
          <DropdownMenu
            showMenu={showMenu}
            onShowMenu={dropdownMenuHandler}
            onEdit={openUpdateMenuHandler}
            onDelete={openDeleteCustomerModal}
          ></DropdownMenu>
        </Fragment>
      )}

      {showDeleteCustomerModal &&
        createPortal(
          <DeleteUserModal
            onDelete={deleteCustomerHandler}
            onCancel={cancelDeleteCustomer}
          ></DeleteUserModal>,
          destination
        )}
    </Fragment>
  );
}

export default CustomerRow;
