import { Fragment } from "react";
import styles from "./Modal.module.css";
import Overlay from "./Overlay";
import Button from "../UI/Button";
function DeleteUserModal(props) {
  return (
    <Fragment>
      <Overlay onClose={props.onCancel}></Overlay>
      <div className={styles['modal-box']}>
        <p>Do you really want to delete the customer?</p>
        <div className={styles["button-container"]}>
          <Button buttonText="Yes" additionalClass="page-button" clickHandler={props.onDelete}>
            
          </Button>
          <Button buttonText="No" additionalClass="page-button" clickHandler={props.onCancel}>
            
          </Button>
        </div>
      </div>
    </Fragment>
  );
}

export default DeleteUserModal;
