import React, { useEffect } from "react";
import styles from "./DropDownMenu.module.css";

function DropdownMenu(props) {
  useEffect(() => {
    if (props.showMenu) {
      document.addEventListener("click", props.onShowMenu);
    }
    return () => {
      document.removeEventListener("click", props.onShowMenu);
    };
  }, [props.showMenu]);

  return (
    <td>
      <button
        className={
          props.showMenu
            ? styles["dropdownmenu-button--active"]
            : styles["dropdownmenu-button"]
        }
        onClick={props.onShowMenu}
      >
        <ion-icon name="caret-back-outline"></ion-icon>
      </button>
      {props.showMenu ? (
        <nav className={styles["dropdownmenu-box"]}>
          <a
            className={styles["dropdownmenu-link"]}
            href="#"
            onClick={props.onEdit}
          >
            Edit
          </a>

          <a
            className={styles["dropdownmenu-link"]}
            href="#"
            onClick={props.onDelete}
          >
            Delete
          </a>
        </nav>
      ) : null}
    </td>
  );
}

export default DropdownMenu;
