import React, { Fragment, useEffect, useState } from "react";
import "./App.css";
import { createPortal } from "react-dom";
import { Link, Route, Switch } from "react-router-dom";
import { disableBodyScroll, enableBodyScroll } from "body-scroll-lock";

import AddCustomer from "./components/Customer/AddCustomer";
import CustomerTable from "./components/Customer/CustomerTable";
import Header from "./components/UI/Header";
import ErrorModal from "./components/Modal/ErrorModal";
import Button from "./components/UI/Button";

function App() {
  const customersOnPage = 10;
  const [customerList, setCustomerList] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [getError, setGetError] = useState(null);
  const [deleteError, setDeleteError] = useState(false);
  const [search, setSearch] = useState("");
  const [displayList, setDisplayList] = useState([]);
  const [pageNumber, setPageNumber] = useState(1);
  const [numberOfPages, setNumberOfPages] = useState(1);
  const [lowerIndex, setLowerIndex] = useState(0);

  async function fetchCustomersHandler() {
    setIsLoading(true);
    setGetError(null);
    try {
      const response = await fetch("http://3.65.24.123:8080/customers");
      if (!response.ok) {
        throw new Error("Failed to fetch");
      }
      const data = await response.json();
      setCustomerList(data);
    } catch (error) {
      setGetError(error.message);
    }
    setIsLoading(false);
  }

  useEffect(() => {
    fetchCustomersHandler();
  }, []);

  useEffect(()=>{
    switchLists()
  },[customerList,search]);
  
  async function deleteCustomerHandler(deletedCustomer) {
    setDeleteError(false);
    try {
      const response = await fetch("/customers", {
        method: "DELETE",
        body: JSON.stringify(deletedCustomer),
        headers: {
          "Content-Type": "application/json",
        },
      });
      if (!response.ok) {
        throw new Error("Something went wrong.");
      }
      deleteCustomerFromList(deletedCustomer);
    } catch (error) {
      openDeleteModal();
    }
  }
  function openDeleteModal() {
    setDeleteError(true);
    disableBodyScroll(document);
  }

  function closeDeleteModal() {
    setDeleteError(false);
    enableBodyScroll(document);
  }

  function deleteCustomerFromList(deletedCustomer) {
    setCustomerList((prevState) => {
      const newArrayState = prevState.filter((customer) => {
        return customer.id !== deletedCustomer.id;
      });
      return newArrayState;
    });
  }

  function addCustomerHandler(customer) {
    const newArrayState = [...customerList, customer];
    setCustomerList(newArrayState);
  }

  function updateCustomerList(customer) {
    setCustomerList((prevState) => {
      let low = 0;
      let high = prevState.length - 1;
      while (low <= high) {
        let mid = Math.floor((high + low) / 2);
        if (prevState[mid].id === customer.id) {
          const newCustomerArray = [...prevState];
          newCustomerArray[mid] = customer;
          return newCustomerArray;
        } else if (prevState[mid].id < customer.id) {
          low = mid + 1;
        } else {
          high = mid - 1;
        }
      }
      return prevState;
    });
  }

  function searchHandler(event) {
    setLowerIndex(0);
    setPageNumber(1);
    setSearch(event.target.value);
    switchLists(customerList, event.target.value);
  }
  function calcNumberOfPages(list) {
    return Math.ceil(list.length / customersOnPage);
  }

  function cool(list) {
    const newNumberOfPages = calcNumberOfPages(list);
    setNumberOfPages(newNumberOfPages);
    if (
      pageNumber>1 &&
      list.length % customersOnPage === 0 &&
      pageNumber === numberOfPages
    ) {
      lastPage(newNumberOfPages);
    }
    setDisplayList(list);
  }

  function switchLists() {
    if (!search) {
      cool(customerList );
    } else {
      const searchList = customerList.filter((customer) => {
        return (
          customer.firstName?.toLowerCase().includes(search) ||
          customer.lastName?.toLowerCase().includes(search) ||
          customer.email?.toLowerCase().includes(search) ||
          customer.phone?.toLowerCase().includes(search)
        );
      });
      cool(searchList);
    }
  }

  function nextPageHandler(event) {
    event.preventDefault();
    if (pageNumber < numberOfPages) {
      setLowerIndex((prevState) => {
        return prevState + customersOnPage;
      });
      setPageNumber((prevState) => {
        return prevState + 1;
      });
    }
  }

  function previousPageHandler(event) {
    event.preventDefault();
    if (pageNumber > 1) {
      setLowerIndex((prevState) => {
        return prevState - customersOnPage;
      });
      setPageNumber((prevState) => {
        return prevState - 1;
      });
    }
  }

  function lastPage(numberOfPages) {
    setPageNumber(() => {
      return numberOfPages;
    });
    setLowerIndex(() => {
      return (numberOfPages - 1) * customersOnPage;
    });
  }

  function lastPageHandler(event) {
    event.preventDefault();
    lastPage(numberOfPages);
  }

  function firstPageHandler(event) {
    event.preventDefault();
    setPageNumber(() => {
      return 1;
    });
    setLowerIndex(() => {
      return 0;
    });
  }

  function tableIndex() {
    return pageNumber === numberOfPages && numberOfPages > 1
      ? displayList.length - customersOnPage
      : lowerIndex;
  }

  function pageDisplay() {
    return displayList.slice(
      pageNumber === numberOfPages ? -customersOnPage : lowerIndex,
      lowerIndex + customersOnPage
    );
  }

  const dissableNextButtons =pageNumber === numberOfPages ;
  const noCustomersFound=!isLoading && !getError && displayList.length === 0 ;
  const destination = document.getElementById("modal-destination");

  return (
    <Fragment>
      <Header></Header>
      <Switch>
        <Route path="/add-customer">
          <section>
            <AddCustomer onAddCustomer={addCustomerHandler}></AddCustomer>
          </section>
        </Route>
        <Route path="/" exact>
          <section>
            {deleteError &&
              createPortal(
                <ErrorModal
                  errorMessage="Something went wrong"
                  onClose={closeDeleteModal}
                ></ErrorModal>,
                destination
              )}
            <div className="top-container">
              <Link className="add-customer-link" to="/add-customer">
                Add
              </Link>
              <input
                className="search-input"
                value={search}
                onChange={searchHandler}
                placeholder="Search"
              ></input>
            </div>
            {!isLoading && customerList.length > 0 && (
              <Fragment>
                <CustomerTable
                  onUpdate={updateCustomerList}
                  index={tableIndex()}
                  customerList={pageDisplay()}
                  onDelete={deleteCustomerHandler}
                ></CustomerTable>
                {!noCustomersFound&&
                <div className="buttons-container">
                  <Button
                    buttonText="&lt;&lt;"
                    clickHandler={firstPageHandler}
                  ></Button>
                  <Button
                    buttonText="Previous"
                    clickHandler={previousPageHandler}
                  ></Button>
                  <p className="page-number">{pageNumber}</p>
                  <Button
                    buttonText="Next"
                    clickHandler={nextPageHandler}
                    additionalClass={dissableNextButtons && "disabled"}
                  ></Button>
                  <Button
                    buttonText="&gt;&gt;"
                    clickHandler={lastPageHandler}
                    additionalClass={dissableNextButtons && "disabled"}
                  ></Button>
                </div>}
              </Fragment>
            )}
            {/*message when data is loading */}
            {isLoading && <p>Loading...</p>}
            {/*message if there are no customers */}
            {noCustomersFound && (
              <p>No customers found.</p>
            )}
            {/*error message if loading finished with error*/}
            {!isLoading && displayList.length === 0 && getError && (
              <p>{getError}</p>
            )}
          </section>
        </Route>
        <Route>
          <h2>Page not found.</h2>
        </Route>
      </Switch>
    </Fragment>
  );
}

export default App;
